<template>
  <main-layout>
    <div class="user-view">
      <div class="page-header">
        <h1 class="page-title">个人中心</h1>
      </div>

      <div class="user-container">
        <!-- 侧边菜单 -->
        <div class="user-sidebar">
          <div class="user-info">
            <el-avatar :size="80" icon="el-icon-user"></el-avatar>
            <h3>{{ userInfo.nickname || '用户' }}</h3>
            <p class="user-phone">{{ userInfo.phone | formatPhone }}</p>
            <p class="user-level">
              <el-tag size="small" type="warning">{{ userInfo.level }}</el-tag>
            </p>
          </div>

          <el-menu
            :default-active="activeMenu"
            class="user-menu"
            @select="handleMenuSelect"
          >
            <el-menu-item index="profile">
              <i class="el-icon-user"></i>
              <span>基本信息</span>
            </el-menu-item>
            <el-menu-item index="works">
              <i class="el-icon-picture"></i>
              <span>我的作品</span>
            </el-menu-item>
            <el-menu-item index="collections">
              <i class="el-icon-star-off"></i>
              <span>我的收藏</span>
            </el-menu-item>
            <el-menu-item index="orders">
              <i class="el-icon-shopping-cart-full"></i>
              <span>订单记录</span>
            </el-menu-item>
            <el-menu-item index="security">
              <i class="el-icon-lock"></i>
              <span>安全设置</span>
            </el-menu-item>
          </el-menu>
        </div>

        <!-- 内容区域 -->
        <div class="user-content">
          <!-- 基本信息 -->
          <div v-if="activeMenu === 'profile'" class="content-section">
            <h2 class="section-title">基本信息</h2>
            <div class="profile-info">
              <el-form :model="userInfo" label-width="80px" ref="profileForm">
                <el-form-item label="头像">
                  <el-avatar :size="100" icon="el-icon-user"></el-avatar>
                  <el-button size="small" type="primary" plain style="margin-left: 20px">更换头像</el-button>
                </el-form-item>
                <el-form-item label="昵称">
                  <el-input v-model="userInfo.nickname" placeholder="请输入昵称"></el-input>
                </el-form-item>
                <el-form-item label="手机号">
                  <el-input v-model="userInfo.phone" disabled></el-input>
                </el-form-item>
                <el-form-item label="邮箱">
                  <el-input v-model="userInfo.email" placeholder="请输入邮箱"></el-input>
                </el-form-item>
                <el-form-item label="个人简介">
                  <el-input type="textarea" v-model="userInfo.bio" :rows="4" placeholder="请输入个人简介"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="saveProfile">保存修改</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>

          <!-- 我的作品 -->
          <div v-if="activeMenu === 'works'" class="content-section">
            <h2 class="section-title">我的作品</h2>
            <div class="works-filter">
              <el-radio-group v-model="worksFilter" size="medium">
                <el-radio-button label="all">全部作品</el-radio-button>
                <el-radio-button label="published">已发布</el-radio-button>
                <el-radio-button label="draft">草稿箱</el-radio-button>
              </el-radio-group>
              <el-button type="primary" icon="el-icon-plus" @click="createNewWork">创建新作品</el-button>
            </div>

            <div v-if="userWorks.length > 0" class="works-grid">
              <div v-for="work in userWorks" :key="work.id" class="work-item">
                <!-- 此处为图片占位 -->
                <div class="image-placeholder" :style="{ backgroundColor: work.bgColor }">
                  <i class="el-icon-picture-outline"></i>
                </div>
                <div class="work-info">
                  <h3>{{ work.title }}</h3>
                  <p class="work-date">{{ work.date }}</p>
                  <div class="work-actions">
                    <el-button size="mini" type="text" icon="el-icon-edit">编辑</el-button>
                    <el-button size="mini" type="text" icon="el-icon-delete" @click="deleteWork(work)">删除</el-button>
                  </div>
                </div>
              </div>
            </div>

            <div v-else class="empty-state">
              <i class="el-icon-picture"></i>
              <p>暂无作品</p>
              <el-button type="primary" @click="createNewWork">立即创建</el-button>
            </div>
          </div>

          <!-- 我的收藏 -->
          <div v-if="activeMenu === 'collections'" class="content-section">
            <h2 class="section-title">我的收藏</h2>
            <div v-if="userCollections.length > 0" class="collections-grid">
              <div v-for="collection in userCollections" :key="collection.id" class="collection-item">
                <!-- 此处为图片占位 -->
                <div class="image-placeholder" :style="{ backgroundColor: collection.bgColor }">
                  <i class="el-icon-picture-outline"></i>
                </div>
                <div class="collection-info">
                  <h3>{{ collection.title }}</h3>
                  <p>作者: {{ collection.author }}</p>
                  <p class="collection-date">收藏于: {{ collection.date }}</p>
                  <div class="collection-actions">
                    <el-button size="mini" type="text" icon="el-icon-view" @click="viewCollection(collection)">查看</el-button>
                    <el-button size="mini" type="text" icon="el-icon-star-off" @click="cancelCollection(collection)">取消收藏</el-button>
                  </div>
                </div>
              </div>
            </div>

            <div v-else class="empty-state">
              <i class="el-icon-star-off"></i>
              <p>暂无收藏</p>
              <el-button type="primary" @click="goToPaintings">去浏览画作</el-button>
            </div>
          </div>

          <!-- 订单记录 -->
          <div v-if="activeMenu === 'orders'" class="content-section">
            <h2 class="section-title">订单记录</h2>
            <div v-if="userOrders.length > 0" class="orders-list">
              <el-table :data="userOrders" style="width: 100%">
                <el-table-column prop="id" label="订单号" width="180"></el-table-column>
                <el-table-column prop="name" label="订单名称"></el-table-column>
                <el-table-column prop="date" label="购买日期" width="180"></el-table-column>
                <el-table-column prop="amount" label="金额" width="120"></el-table-column>
                <el-table-column prop="status" label="状态" width="120">
                  <template slot-scope="scope">
                    <el-tag :type="scope.row.status === '已支付' ? 'success' : 'warning'">{{ scope.row.status }}</el-tag>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="150">
                  <template slot-scope="scope">
                    <el-button size="mini" type="text" @click="viewOrder(scope.row)">查看详情</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>

            <div v-else class="empty-state">
              <i class="el-icon-shopping-cart-full"></i>
              <p>暂无订单记录</p>
            </div>
          </div>

          <!-- 安全设置 -->
          <div v-if="activeMenu === 'security'" class="content-section">
            <h2 class="section-title">安全设置</h2>
            <div class="security-settings">
              <el-card class="security-item">
                <div slot="header" class="clearfix">
                  <span>修改密码</span>
                </div>
                <el-form :model="passwordForm" label-width="100px" ref="passwordForm">
                  <el-form-item label="原密码" prop="oldPassword">
                    <el-input type="password" v-model="passwordForm.oldPassword" show-password></el-input>
                  </el-form-item>
                  <el-form-item label="新密码" prop="newPassword">
                    <el-input type="password" v-model="passwordForm.newPassword" show-password></el-input>
                  </el-form-item>
                  <el-form-item label="确认新密码" prop="confirmPassword">
                    <el-input type="password" v-model="passwordForm.confirmPassword" show-password></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="changePassword">修改密码</el-button>
                  </el-form-item>
                </el-form>
              </el-card>

              <el-card class="security-item">
                <div slot="header" class="clearfix">
                  <span>账号绑定</span>
                </div>
                <div class="binding-item">
                  <div class="binding-info">
                    <i class="el-icon-mobile-phone"></i>
                    <div>
                      <h4>手机绑定</h4>
                      <p>已绑定：{{ userInfo.phone | formatPhone }}</p>
                    </div>
                  </div>
                  <el-button size="small" type="primary" plain>修改</el-button>
                </div>
                <div class="binding-item">
                  <div class="binding-info">
                    <i class="el-icon-message"></i>
                    <div>
                      <h4>邮箱绑定</h4>
                      <p>{{ userInfo.email ? '已绑定：' + userInfo.email : '未绑定' }}</p>
                    </div>
                  </div>
                  <el-button size="small" type="primary" plain>{{ userInfo.email ? '修改' : '绑定' }}</el-button>
                </div>
              </el-card>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main-layout>
</template>

<script>
import MainLayout from '@/components/MainLayout.vue'

export default {
  name: 'UserView',
  components: {
    MainLayout
  },
  data() {
    return {
      activeMenu: 'profile',
      userInfo: {
        phone: sessionStorage.getItem('userPhone') || '',
        nickname: '艺术爱好者',
        email: '',
        bio: '',
        level: 'VIP会员'
      },
      passwordForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      worksFilter: 'all',
      userWorks: Array.from({ length: 6 }, (_, index) => ({
        id: index + 1,
        title: `我的作品 ${index + 1}`,
        date: new Date(Date.now() - Math.random() * 10000000000).toLocaleDateString(),
        bgColor: this.getRandomColor()
      })),
      userCollections: Array.from({ length: 4 }, (_, index) => ({
        id: index + 1,
        title: `收藏的作品 ${index + 1}`,
        author: `艺术家 ${(index % 5) + 1}`,
        date: new Date(Date.now() - Math.random() * 10000000000).toLocaleDateString(),
        bgColor: this.getRandomColor()
      })),
      userOrders: Array.from({ length: 3 }, (_, index) => ({
        id: `ORDER${10000 + index}`,
        name: index === 0 ? 'VIP会员月卡' : `画作下载 ${index}`,
        date: new Date(Date.now() - Math.random() * 10000000000).toLocaleDateString(),
        amount: index === 0 ? '¥29.9' : '¥9.9',
        status: '已支付'
      }))
    }
  },
  filters: {
    formatPhone(phone) {
      if (!phone) return ''
      return phone.substring(0, 3) + '****' + phone.substring(7)
    }
  },
  methods: {
    handleMenuSelect(key) {
      this.activeMenu = key
    },
    getRandomColor() {
      const colors = ['#e6f7ff', '#f6ffed', '#fff7e6', '#fcf4ff', '#f5f5f5', '#fffbe6', '#fff2e8', '#e6fffb']
      return colors[Math.floor(Math.random() * colors.length)]
    },
    saveProfile() {
      this.$message.success('个人信息保存成功')
    },
    changePassword() {
      if (!this.passwordForm.oldPassword || !this.passwordForm.newPassword || !this.passwordForm.confirmPassword) {
        this.$message.error('请填写完整的密码信息')
        return
      }
      
      if (this.passwordForm.newPassword !== this.passwordForm.confirmPassword) {
        this.$message.error('两次输入的新密码不一致')
        return
      }
      
      this.$message.success('密码修改成功，下次登录请使用新密码')
      this.passwordForm = {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
    },
    createNewWork() {
      this.$message.info('创建作品功能开发中，敬请期待！')
    },
    deleteWork(work) {
      this.$confirm(`确定要删除作品"${work.title}"吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.userWorks = this.userWorks.filter(item => item.id !== work.id)
        this.$message.success('删除成功')
      }).catch(() => {})
    },
    viewCollection(collection) {
      this.$router.push(`/painting/${collection.id}`)
    },
    cancelCollection(collection) {
      this.$confirm(`确定要取消收藏"${collection.title}"吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.userCollections = this.userCollections.filter(item => item.id !== collection.id)
        this.$message.success('已取消收藏')
      }).catch(() => {})
    },
    viewOrder(order) {
      this.$message.info(`订单${order.id}详情功能开发中，敬请期待！`)
    },
    goToPaintings() {
      this.$router.push('/paintings')
    }
  }
}
</script>

<style scoped>
.user-view {
  padding: 20px 0 40px;
}

.page-header {
  margin-bottom: 30px;
}

.page-title {
  font-size: 28px;
  color: #333;
  margin: 0;
}

.user-container {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.user-sidebar {
  width: 250px;
  margin-right: 30px;
  flex-shrink: 0;
}

.user-info {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
  margin-bottom: 20px;
}

.user-info h3 {
  margin: 10px 0 5px;
  font-size: 18px;
  color: #333;
}

.user-phone {
  margin: 0 0 10px;
  color: #999;
  font-size: 14px;
}

.user-level {
  margin: 0;
}

.user-menu {
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.user-content {
  flex: 1;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.section-title {
  font-size: 20px;
  color: #333;
  margin: 0 0 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}

.empty-state {
  text-align: center;
  padding: 50px 0;
  color: #909399;
}

.empty-state i {
  font-size: 64px;
  margin-bottom: 20px;
}

.empty-state p {
  margin-bottom: 20px;
  font-size: 16px;
}

/* 作品样式 */
.works-filter {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.works-grid, .collections-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.work-item, .collection-item {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s;
}

.work-item:hover, .collection-item:hover {
  transform: translateY(-5px);
}

.image-placeholder {
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-placeholder i {
  font-size: 36px;
  color: rgba(255, 255, 255, 0.7);
}

.work-info, .collection-info {
  padding: 12px;
}

.work-info h3, .collection-info h3 {
  margin: 0 0 5px;
  font-size: 16px;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.work-date, .collection-date {
  margin: 0 0 10px;
  font-size: 12px;
  color: #999;
}

.collection-info p {
  margin: 0 0 5px;
  font-size: 14px;
  color: #666;
}

.work-actions, .collection-actions {
  display: flex;
  justify-content: flex-end;
}

/* 安全设置样式 */
.security-settings {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  gap: 20px;
}

.security-item {
  margin-bottom: 20px;
}

.binding-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  border-bottom: 1px solid #eee;
}

.binding-item:last-child {
  border-bottom: none;
}

.binding-info {
  display: flex;
  align-items: center;
}

.binding-info i {
  font-size: 24px;
  margin-right: 15px;
  color: #409EFF;
}

.binding-info h4 {
  margin: 0 0 5px;
  font-size: 16px;
}

.binding-info p {
  margin: 0;
  font-size: 14px;
  color: #666;
}

@media screen and (max-width: 768px) {
  .user-container {
    flex-direction: column;
  }
  
  .user-sidebar {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }
  
  .user-content {
    width: 100%;
  }
  
  .security-settings {
    grid-template-columns: 1fr;
  }
}
</style>